import { GETNUMINFO } from '../../../utils/api.js'
export default {
	data() {
		return {
			phoneNum: '',
			myPhone: ''
		}
	},
	onLoad() {
		uni.showToast({
			title: '加载中..',
			icon: 'none',
			mask: false
		})
		// 获取呼叫转接号码
		this.getPhoneNum()
	},
	methods: {
		getPhoneNum(){
			const params = {}
			this.$request.url_request(GETNUMINFO,params,'GET',res=>{
				this.phoneNum = JSON.parse(res.data).data.line
				this.myPhone = JSON.parse(res.data).data.mobile
				uni.hideToast()
			},err =>{})
		},
	}
}
